@import "../css/variables";

.daddy2 {
  background-image: url('../images/register.jpg');
  background-size: 100%;
}


.contentRegister {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 800px;
}


.Login {
  min-width: 320px;
  width: 100%;
  max-width: 400px;
  height: 450px;
  margin-top: 25px;
  position: relative;
  overflow: hidden;
  .bg-image {
    /*width: 100%;
    height: 100%;
    position: absolute;
    background-image: url("https://c8.alamy.com/comp/CFEGYY/yellow-and-red-textured-golden-design-background-CFEGYY.jpg");
    background-size: cover;
    background-position: center; !* Center the image *!
    filter: blur(24px);*/
  }
  .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, #f8f8ff, #696969);
  }
  .login-form {
    position: absolute;
    width: 100%;
    height: 100%;
    @include flex-display(column, center, center);
  }
  .login-icon {
    background-color: white;
    border-radius: 50%;
    margin-bottom: 10px;
    width: 70px;
    height: 70px;
    @include flex-display(row, center, center);
    .MuiSvgIcon-root {
      width: 50px;
      height: 50px;
      color: black;
    }
  }
  .success {
    .MuiSnackbarContent-root {
      background-color: #01a811;
    }
  }
  .failure {
    .MuiSnackbarContent-root {
      background-color: #ff5049;
    }
  }
  // override
  &.MuiPaper-rounded {
    border-radius: 8px;
  }
  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus {
    -webkit-text-fill-color: white;
    -webkit-box-shadow: 0 0 0 1000px #9431b0 inset;
    background-color: transparent !important;
  }
}
